import React, { useState } from "react";
import { Container, Typography, Grid, makeStyles } from "@material-ui/core";
import ComingSoon from "../components/ComingSoon";
import { bio } from "../content.json";
const useStyles = makeStyles((theme) => ({
	profilePic: {
		// height: 250,
		width: "100%",
		boxShadow: theme.shadows[1],
	},
}));
let images = [
	{ id: "biking", path: "images/biking.jpg" },
	{ id: "skiing", path: "images/skiing.jpg" },
	{ id: "standing", path: "images/standing.jpg" },
	{ id: "sitting", path: "images/sitting.jpg" },
];
const getRandom = () => Math.floor(Math.random() * images.length);

export default function About() {
	const classes = useStyles();
	const [index, setIndex] = useState(getRandom());
	const setRandom = () => {
		let i = index;
		while (i === index) {
			i = getRandom();
		}
		setIndex(i);
	};
	return (
		<Container>
			<Grid container direction="row" spacing={2}>
				<Grid item xs>
					<Typography variant="h4">About Me</Typography>
					<Typography>{bio}</Typography>
				</Grid>
				<Grid item xs={12} md={3}>
					<img
						alt=""
						src={`${process.env.PUBLIC_URL}/${images[index].path}`}
						className={classes.profilePic}
						onClick={setRandom}
					/>
				</Grid>
			</Grid>
		</Container>
	);
}
